@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: #212529;
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;
}

body {
  font-family: "Roboto Condensed" sans-serif;
}
/********************************NAVBAR********************************/
nav {
  z-index: 2;
}

.logo {
  width: 16.25rem;
}

/********************************NAVLINKS*****************************/
nav a.nav-link {
  font-size: .8rem;
  font-weight: 500;
  letter-spacing: .0375rem;
  cursor:pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid #212529;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important; 
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.avatar {
  display: block;
  margin: 25px;
  border-radius: 45px;
  box-sizing: border-box;
  background-color: #DDD;
  border: 5px solid #f9ab00;
  border: 5px solid var(--primary-orange);
  margin: 25px;
  height: 55px;
  width: 55px;
}

/*******************ABOUT ME PORTRAIT************************************/
.portrait {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.aboutCopy {
  font-family: "Roboto Condensed" sans-serif;
  font-size: medium;
}

.aboutMeHeader {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
  font-family: "Roboto Condensed" sans-serif;
}

.portrait-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

/*******************ABOUT ME PORTRAIT************************************/
.services {
  background-color: darkgrey;
}

.header-wrapper {
    position: relative;
    background: url(/Portfolio_React_v2/static/media/wall-and-laptop-background.f9e274f4.jpg);
    background-size: cover;
    background-position: center;
    height: 90vh;
}

.main-info {
    display: -webkit-flex;
    display: flex;
    position:absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-family: "Roboto Condensed" sans-serif;
    z-index: 2;
}

.main-info h1 {
    color: var(--primary-orange);
    text-transform: uppercase;
}

@media(max-width: 768px) {
    .main-info h1 {
        font-size: 1.7rem !important;
    }
}

@media(max-width: 568px) {
    .main-info h1 {
        font-size: 1.3rem !important;
    }
}

/**************************************TYPED TEXT******************************************/
.typed-text {
    font-size: 2rem;
    color: var(--primary-white); 
}

@media(max-width: 768px) {
    .typed-text {
        font-size: 1.3rem !important;
    }
}

/**************************************HEADER MAIN OFFER BUTTON******************************************/
.btn-main-offer {
    border: 1px solid var(--primary-red);
    text-transform: uppercase;
    border-radius: 0;
    padding: .625rem .75rem .75rem .75rem;
    color: var(--primary-white);
    margin: 2rem;
    font-size: medium;
    text-decoration: none;
}

.btn-main-offer:hover {
    text-decoration: none;
    background-color: var(--primary-hover-red);
    color: var(--primary-white);
    transition: 0.2s ease-in-out;
}

/**************************************PARTICLES******************************************/
.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

@media(max-width: 768px) {
    .particles-canvas{
        display: none;
    }
}
.card-text, .card-title, .icon {
    color: white;
    font-family: "Roboto Condensed" sans-serif;
}

.card-text {
    font-size: .8rem;
}

.card-title, .icon {
    -webkit-justify-content: center;
            justify-content: center;
    color: var(--primary-orange);
}

.card {
    background-color: var(--primary-navy);
    border-radius: 2%;
    border-bottom: 0.1875rem solid var(--primary-orange);
}

.card:hover {
    background-color: white;
    border-radius: 2%;
    border-bottom: 0.1875rem solid var(--primary-navy);
    transition: 0.3s ease-in-out;
  }
  

@media(max-width: 768px) {
    .card-body {
        padding: 1.3rem !important;
    }
}
.exprience_card_title {
    margin-top: 0.3125rem;
    color: var(--primary-red);
    font-weight: 600;
}

.exprience_card_body {
    font-family: "Roboto Condensed" sans-serif;
    font-size: small;
}
/*******************EXPERIENCE************************************/
.experience {
    padding: 0.7rem;
}

.experience-wrapper {
    position: relative;
    padding: 3.125rem 0rem;
    overflow: hidden;
}

.experience h1 {
    text-transform: uppercase;
    color: var(--primary-orange);
}

.experience-wrapper::before {
    position: absolute;
    content: "";
    top: 0;
    left: 50%;
    margin-left: -0.0625rem !important;
    width: 0.125rem;
    height: 100%;
    background: var(--primary-grey);
    z-index: 1;
}

.timeline-block {
    width: calc(50% + 5rem);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    clear: both;
}

.timeline-block-right {
    float: right;
    width: 50%;
}

.timeline-block-left {
    float: left;
    width: 50%;
    direction: rtl;
}

.marker {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.3125rem solid var(--primary-navy);
    background: var(--primary-orange);
    margin-top: 0.625rem;
    z-index: 3;
}

.timeline-content {
    width: 95%;
    padding: 0 0.9375rem;
    color: var(--primary-dark-grey);
    direction: ltr;
}

/***********************************Media**********************/

@media(max-width: 768px) {
    .experience-wrapper{
        padding-left: 0;
    }
    .experience-wrapper::before{
        left: 0.5rem;
        margin-bottom: 1.875rem;
    }
    .timeline-block-right{
        float: none;
        width: 100%;
    }
    .timeline-block-left {
        float: none;
        direction: ltr;
        width: 100%;
    }
}
.portfolio-wrapper {
    background: var(--primary-light-grey);
    padding: 3rem 0;
}

.portfolio-image {
    width: 15rem;
    height: 9rem;
    border: 1px solid var(--primary-orange);
    padding: 0.5rem;
    z-index: 2;
}

.portfolio-image-box {
    position: relative;
    width: 15rem;
    height: 9rem;
    margin: 1rem;
}

.overflow {
    position: absolute;
    opacity: 0;
    background: var(--primary-black);
    width: 15rem;
    height: 9rem;
    top: 0;
    border: 1px solid var(--primary-red);
    cursor: pointer; 
}

.portfolio-image-box:hover .overflow {
    opacity: 60%;
    transition: 0.3s ease-in-out;
}

.portfolio-icon {
    position: absolute;
    opacity: 0;
    margin-left: 7rem;
    margin-top: 3.6rem;
    color: var(--primary-orange);
    font-size: 1.5rem;
    z-index: 1;
}

.portfolio-image-box:hover .portfolio-icon {
    opacity: 70%;
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

/************************************* OVERFLOW BOX ***************************************/

.portfolio-image-popupbox {
    width: auto;
    border-radius: 50%;
    height: 15rem;
    padding: 0 0.5rem;

}

.popupbox-text, hr {
    margin-top: .5rem;
    margin-left: .5rem;
    font-family: "Roboto Condensed" sans-serif;
    font-size: small;
}

.hyper-link {
    margin-top: .5rem !important;
    font-family: "Roboto Condensed" sans-serif;
    font-size: small;
    cursor: pointer;
    color: var(--primary-orange);
}

.hyper-link:hover {
    color: var(--primary-red);
}

.popupbox-text-icon {
    font-size: large !important;
    color: var(--primary-navy);
}

h3 {
    color: var(--primary-white) !important;
    margin-left: 4px;
}

.header-div {
    background-color: var(--primary-navy) !important;
}
